import styled from 'theme';

interface ITabTitle {
  hasTabs?: boolean;
  isDisabled?: boolean;
}

export const TabTitle = styled.div<ITabTitle>`
  position: relative;
  padding: 8px 10px;
  text-transform: uppercase;
  font-weight: 500;
  cursor: ${({ isDisabled }) => isDisabled ? 'auto' : 'pointer'};
  color: ${({ theme }) => theme.colors.gray};
  font-size: 12px;
  letter-spacing: .4pt;
  user-select: none;
  transition: all .1s linear;

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: ${({ theme }) => theme.colors.lightGray};
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    width: 0;
    background-color: ${({ theme }) => theme.colors.normalAccent};
    transition: all .1s linear;
  }

  ${({ isDisabled, theme }) => !isDisabled && `
    &:hover {
      color: ${theme.colors.normalAccent};

      &:after {
        width: 100%;
      }
    }
  `}

  ${({ isDisabled }) => isDisabled && `
    .title {
      opacity: .5;
    }
  `}

  &.is-active {
    color: ${({ theme }) => theme.colors.normalAccent};
    background-color: ${({ theme }) => theme.colors.lighterGrayHover};
    border-top-right-radius: 2px;
    border-top-left-radius: 2px;
    cursor: default;
    user-select: inherit;

    &:after {
      width: 100%;
    }

    ${({ hasTabs }) => hasTabs && `
      background-color: transparent;

      &:after {
        width: 0;
      }
    `}
  }
`;
